import React, { useEffect, useState } from 'react';
import { ItemWithInfo } from './ItemWithInfo';
import { VoteButton } from './VoteButton';
import { Footer } from './Footer';
import { CircularProgress } from '@material-ui/core';
import { IconButton } from '@material-ui/core';
import { ExpandMore, ExpandLess } from '@mui/icons-material';

const HomeAllCandidates = ({allItems, handleVote}) => {
  const [loading, setLoading] = useState(true)
  const [showVoteText, setShowVoteText] = useState(false);
  const [isClickable, setIsClickable] = useState(true);
  const [buttonTooltip, setButtonTooltip] = useState("");
  
  const [selectedItemId, setSelectedItemId] = useState(null);

  const [isInfoExpanded, setIsInfoExpanded] = useState(false);
  
  const selectCandidateToVote = "Vlj en kandidat fr att kunna rsta"
  const timeoutMessage = "Vnta lite innan du kan rsta igen"

  const toggleInfoExpanded = () => {
    setIsInfoExpanded(!isInfoExpanded);
  };

  const handleSelect = (itemId) => {
    setSelectedItemId(itemId === selectedItemId ? null : itemId);
    if (buttonTooltip != timeoutMessage) {
      setButtonTooltip(selectCandidateToVote)
    }
  };

  const getSelectedItem = () => {
    if (selectedItemId === null) {
      return null;
    }
    var foundItem = allItems.find(item => item.id === selectedItemId);
    if (foundItem){
      return foundItem;
    }
    return null;
  }

  const getFullName = (item) => {
    if (item === null) {
      return null
    }
    return item.firstName + " " + item.lastName
  }

  const handleVoteWithClickability = () => {
    if (!isClickable) {
      return;
    }

    setSelectedItemId(null);

    // Disable clickability
    setIsClickable(false);
    setButtonTooltip(timeoutMessage);

    // Enable clickability after 300ms
    setTimeout(() => {
      setIsClickable(true);
      setButtonTooltip(selectCandidateToVote);
    }, 5000);

    // Handle the click logic
    handleVote(selectedItemId);

    setShowVoteText(true);
    setTimeout(() => {
      setShowVoteText(false);
    }, 20000);
  }

  useEffect(() => {
    // Load initial value on component mount
    setButtonTooltip(selectCandidateToVote);
    setLoading(false);
  }, []);

  const voteText = "Tack fr din rst. Kom ihg att ven rsta i valet till Europaparlamentet den 9 juni!"
  const howToVote = `Hr p issionar.com kan du rsta p den person som du anser vara bst lmpad som EU-kommissionr. Kandidaterna nedan r en sammanstllning av de som det har spekulerats om i media.

Ls mer om kandidaterna och lgg din rst nedan genom att scrolla lngst ner och klicka 'Rsta' efter att du valt den du vill se som kommissionr.`
  const disclaimer = `Belly Button Gazette och issionar.com arbetar endast i rdgivande syfte.
De oansvariga utgivarna fr issionar.com kommer lmna ver rstlngden till regeringen under en ceremoni den 10 juni.`
  const aboutIssionar = `Frst ska kommissionsordfrande utnmnas av Europeiska rdet och godknnas av Europaparlamentet. Helst ska ordfrande komma frn den strsta partigruppen i Europaparlamentet (spitzenkandidat-systemet). Drefter kommer lndernas regeringar att vlja ut sina kommissionrer. 

Vilken portflj vr kommissionr kommer att f beror p vilken bakgrund vr kandidat har. Det r frdelaktigt om kommissionren har lng erfarenhet, helst i ministerroller. 
  
Vem regeringen planerar att utse r nnu oknt, men mnga namn har cirkulerat i media. D det r den nuvarande regeringen som ansvarar fr utnmningen antas Sveriges nsta kommissionr att tillhra ett Tidparti. Mnga lyfter kandidater frn Moderaterna i och med att ingen moderat hittills varit kommissionr. 
  
Kommissionen r den enda EU-institution som kan initiera lagfrslag och har drmed stor makt  men eftersom posterna tillstts av respektive lands regering, gr det inte att rsta p en enskild kommissionr. Fr att demokratisera processen presenterar Belly Button Gazette drfr denna valplattform.
`
const euYellow = "#DDBB00";

  return (
    <div className="content" >
      <div className='home-page-text'>
        Vlj din EU-kommissionr
      </div>
      <div className='home-page-explanation' style={{ paddingBottom: 0 }}>
        <div style={{ height: isInfoExpanded ? "auto" : "200px", overflow: "hidden", cursor: "pointer" }} onClick={toggleInfoExpanded}>
          <div className='home-page-explanation-header'>
            Hur det funkar
          </div>
          <div className='home-page-explanation-text'>
            {howToVote}
          </div>
          <div className='home-page-explanation-header'>
            Hur utses en EU-kommissionr?
          </div>
          <div className='home-page-explanation-text'>
            {aboutIssionar}
            <div style={{ paddingTop: "15px", fontSize: "10pt", fontStyle: "italic" }}>
              {disclaimer}
            </div>
          </div>
        </div>
        <div style={{ height: "1.5rem", width: "100%", display: "flex", justifyContent: "center", cursor: "pointer" }} onClick={toggleInfoExpanded}>
          <IconButton onClick={(e) => {
            e.stopPropagation();
            toggleInfoExpanded();
          }
          }
            >
            {isInfoExpanded ? <ExpandLess style={{ color: euYellow }}/> : <ExpandMore style={{ color: euYellow }}/>}
          </IconButton>

        </div>
      </div>
      {loading ?
      <CircularProgress/> :
      // <div className='item-pair'>
      <div className='item-list'>
        {allItems.map(item => 
          <ItemWithInfo
            key={item.id}
            itemData={item}
            showParty={true}
            isSelected={selectedItemId === item.id}
            handleSelect={() => handleSelect(item.id)}
        />)}
        {showVoteText ?
        <div className='thanks-for-voting'>
          {voteText}
        </div> : <></>
        }
        <div style={{ marginTop: "3rem", marginBottom: "1rem" }}>
          <VoteButton nameOfVotee={getFullName(getSelectedItem())}
                    isDisabled={selectedItemId === null}
                    onClick={handleVoteWithClickability}
                    tooltip={buttonTooltip}/>
        </div>
      </div>}
      <Footer/>
    </div>
  );
};


export default HomeAllCandidates;